import axios from 'axios'
import cookieUtil from 'js-cookie'
const DEBUG_MODE = global.DEBUGMODE //
var URL_PREFIX = DEBUG_MODE ? ' /api' + global.webSite : (global.IPPORT + global.webSite)
export default {
    login: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/h5/user/login', data, headers);
    },
    home: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/h5/user/home', data, headers);
    },
    send: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/verifyCode/send', data, headers);
    },
    orderList: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/h5/order/list', data, headers);
    },
    orderDetail: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/h5/order/detail', data, headers);
    },
    upload: function(data) { //
        // let config = {
        //    headers: {
        //      'Content-Type': 'multipart/form-data'
        //    }
        //  }
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}',
                'Content-Type': 'multipart/form-data'
            }
        }
        return axios.post(URL_PREFIX + '/file/upload', data, headers);
    },
    getCredit: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/h5/credit/identity/info/get', data, headers);
    },
    saveCredit: function(data) { //
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        return axios.post(URL_PREFIX + '/h5/credit/identity/info/save', data, headers);
    },

    login2: function(data) { //商城发布的商品
        return axios.get(URL_PREFIX + '/h5/user/login', { params: data });
    }



}


