import 'babel-polyfill'
import 'lib-flexible/flexible.js'
import Vue from 'vue'
import Mint from 'mint-ui'
import staticConfig from './util/config'
import 'mint-ui/lib/style.css'
import App from './App'
import md5 from './util/md5'
import router from './router'
import store from './store/store'
import FastClick from 'fastclick'
import axios from 'axios'
import zepto from 'webpack-zepto'
import axiosUtil from './api'
import cookieUtil from 'js-cookie'
const DEBUG_MODE = true
global.URL_PREFIX = DEBUG_MODE ? ' /api' : 'http://'



window.axiosUtil = axiosUtil;
window.router = router;
Vue.use(Mint)
FastClick.attach(document.body)
router.beforeEach((to, from, next) => {
if (to.path == '/login') {
next();
return;
}
if (to.matched.some(m => !m.meta.auth)) {
if (true) { //! global.DEBUGMODE
axiosUtil.isLogin().then(data => {
var message = data
if (message.code != global.tokenLoseCode) {
next()
} else {
next({ path: '/login', query: { referrer: to.fullPath } })
}
})
} else {
next()
}
} else if (to.path == '/login') {
next()
} else {
next()
}
})
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>'
})
window.Vue = Vue;
window.$ = zepto;

window.md5 = md5;
window.cookieUtil = cookieUtil;


