	import custHeader from  'components/custHeader'
		export default{
			data(){
				return {
					title:"洋葱金融系统",
					fileImg:global.fileImg,
					banner1:"首页banner",
					borrowImg:"借钱",
					iconBill:'账单-1',
					alreadyApply:'',
					isApply:true,
					bannerTitle:"海富钱包-您的应急周转钱包！",
					applyType:1,
				}
			},
			created:function(){
				this.home();
			},
			 components:{
    			custHeader
  			},
  			methods:{
  				goBillList:function(){
  					this.$router.push("/bill/billList")
  				},
  				goApply:function(){
					  axiosUtil.home().then(data=>{
						  if(data.code===0){
							  if(this.applyType==1){
								  this.$router.push('/formList');
							  }else if(this.applyType==2){
								  this.$router.push('/formList');
							  }else if(this.applyType==3){
								  this.$router.push('/formList');
							  }
						  }
					  })
  					
				  },
				home:function(){
					axiosUtil.home().then(data=>{
						if(data.code===0){
							this.bannerTitle=data.data.headerTitle;
							this.alreadyApply=data.data.picTitle;			
							this.applyType=data.data.applyType;
						}
					})
				}  
  			}
		}
	


