	export default{
		data(){
			return {
				mobile:'',
					ifFocus:false,
					closeIcon:require('assets/images/关闭-3@2x.png'),
			}
		},
		props:{
			title:{
				type:String,
				default:"头",
			},
			type:{
				type:String,
				default:"text"
			},
			value:{
				type:String,
				default:''
			}
		},
		watch:{
			mobile:function(){
				
				this.$emit('input', this.mobile)
			}
		},
		
		computed:{
  				showTitle:function(){
  					return this.mobile==="" && !this.ifFocus
  				},
  				focusCon:function(){
  					return this.ifFocus;
  				},
  				inputStr:function(){
  					return this.mobile!==""
  				}
  			},
		methods:{
  				inputFocus:function(){
  					this.ifFocus=true;
  				},
  				getFocus:function(){
  					this.$refs.inp.focus();
  				},
  				inputBlur:function(){
  					this.ifFocus=false;
  				},
  				clearInt:function(){
  					this.mobile="";
  					this.ifFocus=false;
  				}
  				
  			}
	}




