import axios from 'axios'
import Qs from 'qs'
import cookieUtil from 'js-cookie'
import modal1 from './modal1.js'
import modal2 from './modal2.js'
axios.defaults.timeout = 5000 // 响应时间
axios.defaults.withCredentialst = true
    // axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8' // 配置
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8' // 配置

// POST传参序列化
axios.interceptors.request.use(
    config => {
        if (config.method === 'post' && config.headers.post['Content-Type'] === 'application/x-www-form-urlencoded;charset=UTF-8' && !config.headers['Content-Type']) {
            config.data = Qs.stringify(config.data)
        }
        if (cookieUtil.get('csrfToken') && !(/^(GET|HEAD|OPTIONS|TRACE)$/.test(config.method))) { // 判断是否存在token，如果存在的话，则每个http header都加上token
            config.headers['x-csrf-token'] = cookieUtil.get('csrfToken')
        }
        return config
    },
    error => {
        return Promise.reject(error)
    })

// http response 拦截器
axios.interceptors.response.use(
    response => {
        console.log(response)
        if (response.data && response.data.code && response.data.code == 201) { // 约定-2未登录
            // 跳转到登录页面
            router.push('/login')
            return Promise.reject({ code: 201 })
        }

        return response.data
    },
    error => {
        if (error.response) {
            switch (error.response.status) {
                case 401:
                    break // 假定401为约定好的后台session失效的状态吗，页面重新跳转到登陆页清掉cookie缓存
                case 500:
                    break
                case 403: // 未登录
            }
            return Promise.reject(error.response.data) // 返回接口返回的错误信息
        } else {
            console.log(error)
        }
    })
const DEBUG_MODE = global.DEBUGMODE //
var URL_PREFIX = DEBUG_MODE ? ' /api' + global.webSite : (global.IPPORT + global.webSite)
export default {
    isLogin(data) {
        var headers = {
            headers: {
                basicParams: '{"channel":"20180324162850527004","token":"' + cookieUtil.get("token") + '","platform":"h5"}'
            }
        }
        if (DEBUG_MODE) {
            return axios.post(URL_PREFIX + '/h5/user/home', data, headers);
        }
        return axios.post(URL_PREFIX + '/h5/user/home', data, headers);
    },
    ...modal1,
    ...modal2

}


