  import custHeader from  'components/custHeader'
export default {
  data () {
    return {
      title:"账单",
      fileImg:global.fileImg,
      billList:[{
        amount:"100.00",
        repaymentDt:"2018-10-10",
        orderStatus:1,
        viewType:0
      }]
    }
  },
  created:function(){
    axiosUtil.orderList().then(data=>{
      if(data.code===0){
        this.billList=data.data.orderListVos;
      }
    })
  },
   components:{
          custHeader
        },
        methods:{
          getIconByStatus:function(statue){
          return   this.fileImg+"已逾期";
          },
          gobillDetail:function(item){
           this.$router.push({name:"billDetail",params:{billNumber:item.orderNumb}})
          }
        }
}



