import Vue from 'vue'
  import custHeader from  'components/custHeader'
  import CommonTitle from "components/CommonTitle";
  import { Cell } from 'mint-ui';

Vue.component(Cell.name, Cell);
export default {
  data () {
    return {
     
      bill:{
        amount:"",
        borrowPurpose:"",
        borrowerIdentityCode:"",
        borrowerName:"",
        borrowingDuration:"",
        buttonTitle:"",
        buttonUrl:"",
        contractTitle:"",
        contractUrl:"",
        extendOrderDetailVos:"",
        headerTitle:"",
        realCapital:"",
        repaymentDt:"",
        totalInterestRatio:"",
        visible:false,
    
      },
      extendOrderDetailVos:[],
      otherUrl:'',
        closeImgSrc:"关闭",
      fileImg:global.fileImg,
      showIfr:false,
    }
  },
  computed:{
    title:function(){
      return this.bill.headerTitle;
    }
  },
  created:function(){
     axiosUtil.orderDetail({orderNumb:this.$route.params.billNumber}).then(data=>{
      if(data.code===0){
        this.bill=data.data;
        if(this.bill.extendOrderDetailVos1!==null){
           this.extendOrderDetailVos=this.bill.extendOrderDetailVos;
        }
       
      }
    })
  },
   components:{
          custHeader,
          CommonTitle
        },
        methods:{
          goUrl:function(){
            ///window.open(this.bill.buttonUrl);
            this.otherUrl=this.bill.buttonUrl;
            this.showIfr=true;
          },
          getIconByStatus:function(statue){
          return   require('assets/images/已逾期@2x.png')
          },
          closeIfr:function(){
            this.showIfr=false;
          }

        }
}



