import demo from '@/views/index/demo'
import index from '@/views/index/index'
import login from '@/views/admin/login'
import billList from '@/views/bill/billList'
import billDetail from '@/views/bill/billDetail'
import upload from '@/views/bill/upload'
let routes = [{
  path: '/',
  name: 'index',
  component: index,
  meta: {
    auth: false
  }
},
{
  path: '/demo',
  name: 'demo',
  component: demo,
  meta: {
    auth: false
  }
},
{
  path: '/login',
  name: 'login',
  component: login,
  meta: {
    auth: false
  }
},
{
  path: '/formList',
  name: 'formList',
  component: () =>
            import('@/views/FormList/FormList'),
  meta: {
    auth: false
  }
},
{
  path: '/bill/billList',
  name: 'billList',
  component: billList,
  meta: {
    auth: false
  }
}, {
  path: '/bill/billDetail',
  name: 'billDetail',
  component: billDetail,
  meta: {
    auth: false
  }
},
{
  path: '/bill/upload',
  name: 'upload',
  component: upload,
  meta: {
    auth: false
  }
}
]
const formEnumRoutes = [
  `apply`,
  `personInfo`,
  `credit`,
  'work',
  'eme',
  'zhima',
  'yys',
  'shop',
  'wallet'
]
formEnumRoutes.map(item => {
  routes.push({
    path: `/formList/${item}`,
    name: `${item}`,
    component: () =>
            import(`@/views/FormList/${item}`),
    meta: {
      auth: false
    }
  })
})
export default routes



