		import custHeader from  'components/custHeader'
		import filedInput from  'components/fieldCommpont'
		import banner1 from  'assets/images/index_banner@2x.png'
		import { MessageBox } from 'mint-ui';
		export default{
			data(){
				return {
					title:"洋葱金融系统",
					fileImg:global.fileImg,
					banner1:"首页banner",
					phoneIcon:"手机号",
					codeIcon:"iconfont-验证码",
					phoneTitle:"请输入手机号",
					verifyCodeTitle:"请输入验证码",
					mobile:'',
					verifyCode:'',
					ifFocus:false,
					capMessage:"获取验证码",
					idDisabled:false,
					bannerTitle:"海富钱包-您的应急周转钱包！",
					timer:'',
					
				}
			},
			created:function(){

			},
			 components:{
    			custHeader,
    			filedInput
  			},
  			computed:{
  				valiSuccess:function(){
  					return (this.mobile==="" || this.verifyCode==="");
  				}
				},
				beforeDestroy:function(){
					clearInterval(this.timer)
				},
  			methods:{
  				getCode:function(){
  					var pars=
  						{mobile:this.mobile,
  						type:global.sendType,	
  					}
  					  axiosUtil.send(pars).then(data=>{
  					  	 let time =60;
  					  	 this.capMessage = "重新发送("+time+")"
			          	this.idDisabled = true;

			           this.timer = setInterval(()=>{
			          	console.log(time)
			            time = time - 1;
			            this.capMessage = "重新发送("+time+")"
			            if(!time){
			              // time = 60
			              this.capMessage = '获取验证码'
			              this.idDisabled = false;
			              clearInterval(this.timer)
			            }
			          },1000)
  					  })
  				},
  				login:function(){
  					var pars=
  						{mobile:this.mobile,
  							verifyCode:'1234',
  					}
  					
  					  axiosUtil.login(pars).then(data=>{
  					  	console.log(data);
  					  	if(data.code===0){
									global.token=data.data.token;
									cookieUtil.set("token",data.data.token,{expires:global.expires,path:global.webSite})
									this.$router.push('/')
								}else{
									MessageBox('提示', data.msg);
								}
			         
  					  })
  				}

  				
  			}
		}
	


