import Vue from 'vue'
import custHeader from  'components/custHeader'
import CommonTitle from "components/CommonTitle";
import ButtonWrap from "components/ButtonWrap";
import { Popup } from 'mint-ui';
Vue.component(Popup.name, Popup);
export default {
  data () {
    return {
      title:"洋葱金融系统",
      addImgSrc:"添加",
      closeImgSrc:"关闭",
      fileImg:global.fileImg,
      popupVisible:false,
      popupImgBig:false,
      uploadFile1:'',
      uploadFile2:'',
      uploadFile3:'',
      bigImgUrl:'',
      uploadType:0,
      frontUrl:'',
      backUrl:'',
      holdUrl:''

    }
  },
   components:{
          custHeader,
          CommonTitle,
          ButtonWrap
        },
  created:function(){
    this.getCredit();
  },
  methods:{
    getCredit:function(){
      axiosUtil.getCredit().then(data=>{
       if(data.code==0){
         this.uploadFile1=data.data.frontUrl!=null?data.data.frontUrl:'';
         this.uploadFile2=data.data.backUrl!=null?data.data.backUrl:''
         this.uploadFile3=data.data.holdUrl!=null?data.data.holdUrl:''
       }
      })
    },
     saveCredit:function(){
       var   pars={
         "backUrl": this.backUrl,
          "frontUrl": this.frontUrl,
          "holdUrl": this.holdUrl
       }
      axiosUtil.saveCredit(pars).then(data=>{
       if(data.code==0){
         console.log(data)
       }
      })
    },
    mySubmit:function(){
      this.saveCredit();
    },
    clearUpload:function(type){
      this['uploadFile'+type]='';
    },
    setUploadType:function(uploadType){
      this.uploadType=uploadType;
    },
   showUploadType:function(){
    this.popupVisible=true;
   },
   showBigImg:function(type){
    this.popupImgBig=true;
    this.bigImgUrl=this['uploadFile'+type]
   },
   uploadImg:function(){
    var $ele = this.$refs['fileele'+this.uploadType],
        file = $ele.files[0],
        reader = new FileReader();
    reader.readAsDataURL(file);
    console.log("onload")
    var that=this;
    var formData = new FormData();
    formData.append("name", file);
    axiosUtil.upload(formData).then(data=>{
      if(data.code==0){
        if(this.uploadType==1){
            this.frontUrl=data.data.filePath
        }
         if(this.uploadType==2){
            this.backUrl=data.data.filePath
        }
         if(this.uploadType==3){
            this.holdUrl=data.data.filePath
        }
      }else{
        console.log(data)
      }
      
    })
    reader.onload = function(e){

     that['uploadFile'+that.uploadType]=e.target.result;
    };

   },
    selectTypeVisibleShow3:function(){
      this.selectTypeVisible3=true;
    },
    dissSelectType:function(){
       this.selectTypeVisible3=false;
    },
    sureSelectType:function(){
       this.selectTypeVisible3=false;
    },
    
     selectTypeChange3:function(picker,value){
      

      if(value[0]){
        // if(this.addVis){
        //   this.addForm.province.name=value[0].name;
        //   this.addForm.province.value=value[0].value;
        //   window.areaCity("city",value[0].value).then(data=>{
        //     var items=[];
        //      data.forEach(item=>{
        //                 items.push({value:item.ddItem,name:item.ddItemName});
        //               })
        //      this.selectTypeSlots4=[{
        //           flex:1,
        //           values:items,
        //       }];
        //   })
        // }else{
        //   this.editForm.province.name=value[0].name;
        //   this.editForm.province.value=value[0].value;
        //   window.areaCity("city",value[0].value).then(data=>{
        //     var items=[];
        //      data.forEach(item=>{
        //                 items.push({value:item.ddItem,name:item.ddItemName});
        //               })
        //      this.selectTypeSlots4=[{
        //           flex:1,
        //           values:items,
        //       }];
        //   })
        // }

      }
    },
  },
  computed:{
      isAndroid:function(){
        return window.browser.versions.android;
      },
      valiSuccess:function(){
        return this.uploadFile1==='' || this.uploadFile2==='' || this.uploadFile3===''
      }
  }
}



